/* Header */

.header {
	display: flex;
	align-items: center;
	background-color: $secondary-color;
	padding-left: 30px;
	min-height: 90px;
	box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

	&__logo {
		height: 60px;
		width: fit-content;
		margin-right: 50px;

		img {
			height: 100%;
			width: auto;
		}
	}

	&__nav {
		width: 100%;

		ul {
			display: flex;
			justify-content: space-around;

			a {
				color: white;
				@include font-set(1.5rem, 500);
				text-decoration: none;

				&:hover {
					color: $main-color;
				}
			}
		}
	}
}
